<template>
  <div class="back">
    <app-bar
      back-icon
      title="我的信息"
      hide-user-menu
      hideMessage
      hideDemand
    ></app-bar>
    <v-content style="min-height: calc(100vh - 64px)">
      <v-container>
        <v-card class="mx-auto mt-12" min-height="400" max-width="800">
          <v-card-title>基本信息</v-card-title>
          <div class="pl-10">
            <el-form
              label-position="left"
              label-width="80px"
              :model="form"
              :rules="editUserFormRules"
              ref="editUserFormRef"
            >
              <v-col cols="12" sm="6">
                <el-form-item label="公司名称">
                  <el-input :disabled="true" v-model="form.username"></el-input>
                </el-form-item>
                <el-form-item label="税号" prop="paragraph">
                  <el-input
                    v-model="form.paragraph"
                    @blur="changeUserInfo()"
                  ></el-input>
                </el-form-item>
                <el-form-item label="联系人" prop="contact">
                  <el-input
                    v-model="form.contact"
                    @blur="changeUserInfo()"
                  ></el-input>
                </el-form-item>
                <el-form-item label="联系电话" prop="tel">
                  <el-input
                    v-model="form.tel"
                    @blur="changeUserInfo()"
                  ></el-input>
                </el-form-item>
                <el-form-item label="联系邮箱" prop="mail">
                  <el-input
                    v-model="form.mail"
                    @blur="changeUserInfo()"
                  ></el-input>
                </el-form-item>
              </v-col>
              <v-col cols="12" sm="8">
                <el-form-item label="公司地址" prop="address">
                  <el-input
                    v-model="form.address"
                    @blur="changeUserInfo()"
                  ></el-input>
                </el-form-item>
              </v-col>
            </el-form>
          </div>
        </v-card>
      </v-container>
    </v-content>
    <app-footer></app-footer>
  </div>
</template>

<script>
import AppBar from '@/components/App/AppBar'
import AppFooter from '@/components/App/AppFooter'

export default {
  name: 'PersonalCenter',
  components: {
    AppBar,
    AppFooter
  },
  data() {
    var checkEmail = (rule, value, callback) => {
      const regEmail = /^\w+@\w+(\.\w+)+$/
      if (regEmail.test(value)) {
        // 合法邮箱
        return callback()
      }
      callback(new Error('请输入合法邮箱'))
    }
    // 自定义手机号规则
    var checkMobile = (rule, value, callback) => {
      const regMobile = /^1[34578]\d{9}$/
      if (regMobile.test(value)) {
        return callback()
      }
      // 返回一个错误提示
      callback(new Error('请输入合法的手机号码'))
    }
    return {
      form: {
        username: '',
        paragraph: '',
        contact: '',
        tel: '',
        mail: '',
        address: ''
      },
      editUserFormRules: {
        contact: [
          { required: true, message: '请输入联系人姓名', trigger: 'blur' }
        ],
        paragraph: [
          { required: true, message: '请输入公司税号', trigger: 'blur' }
        ],
        mail: [{ validator: checkEmail, trigger: 'blur' }],
        tel: [
          { required: true, message: '请输入联系电话', trigger: 'blur' },
          { validator: checkMobile, trigger: 'blur' }
        ],
        address: [
          { required: true, message: '请输入公司所在地址', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    async getUserInfo() {
      let res = await this.$http.get('/api/home/thisuser')
      if (res.data.code == 200) {
        this.form.id = res.data.data.id
        this.form.username = res.data.data.username
        this.form.paragraph = res.data.data.paragraph
        this.form.contact = res.data.data.contact
        this.form.tel = res.data.data.tel
        this.form.mail = res.data.data.mail
        this.form.address = res.data.data.address
      } else return false
    },
    async changeUserInfo() {
      this.$refs.editUserFormRef.validate(async valid => {
        if (!valid) return
        let res = await this.$http.post('/api/business/updateBus', this.form, {
          headers: { 'Content-Type': 'application/json' }
        })
        if (res.data.code === 200) {
          this.$message.success('用户信息修改成功！')
        } else {
          this.$message.error('用户信息修改失败')
        }
      })
    }
  },
  mounted() {
    this.getUserInfo()
  }
}
</script>

<style scoped>
.back {
  background-color: #f2f2f2;
}
</style>
